  font-family: "Roboto", "Helvetica Neue", helvetica, arial, sans-serif;
  font-weight: 300;
}
.view-feedback blockquote:before, .view-feedback blockquote:after {
  font-family: helvetica, arial, sans-serif;
  color: #81a5bf;
  font-size: 3em;
  line-height: 0.1em;
}
.view-feedback blockquote:before {
  content: "\201c";
  vertical-align: -.4em;
  margin-left: 0;
  margin-right: 0.2em;
}
.view-feedback blockquote:after {
  content: "\201d";
  vertical-align: -.45em;
  margin-left: 0.2em;
}
.view-feedback blockquote > p:first-child {
  display: inline;
}

.view-articles .views-row,
.view-taxonomy-term .views-row {
  padding-bottom: 7px;
  border-bottom: 1px dashed #ddd;
}
.view-articles .field-name-node-title h2,
.view-taxonomy-term .field-name-node-title h2 {
  margin-top: 0;
}
.view-articles .field-name-field-image img,
.view-taxonomy-term .field-name-field-image img {
  width: 100%;
  border: 1px solid #eee;
  margin: 0 auto;
}
.view-articles .field-name-body p:first-child:first-letter,
.view-taxonomy-term .field-name-body p:first-child:first-letter {
  font-family: "Times New Roman", Times, serif;
  /* Гарнитура шрифта первой буквы */
  font-size: 200%;
  /* Размер шрифта первого символа */
  color: #293749;
  /* Красный цвет текста */
}

@media (min-width: 768px) {
  .view-articles .field-name-field-image,
  .view-taxonomy-term .field-name-field-image {
    max-width: 200px;
    float: left;
    margin: 0 15px 15px 0;
  }
}
.view-portfolio .row .col {
  margin-bottom: 30px;
}
.view-portfolio .wrapper {
  position: relative;
}
.view-portfolio .field-flex-wrap {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  background-color: transparent;
  transition: 150ms ease background-color;
}
.view-portfolio .image {
  margin-bottom: 15px;
  overflow: hidden;
}
.view-portfolio img {
  transform: scale(1);
  transition: 150ms ease transform;
  width: 100%;
}
.view-portfolio .description {
  font-size: 14px;
  line-height: 24px;
  margin: auto;
  padding: 0 24px;
  color: #fff;
  opacity: 0;
  text-align: center;
  transition: 150ms ease opacity;
}
.view-portfolio .title {
  margin-bottom: 0;
  color: #293749;
  font-weight: 300;
  text-align: center;
}
.view-portfolio a:hover {
  text-decoration: none;
}
.view-portfolio a:hover img {
  transform: rotate(-2deg) scale(1.05);
}
.view-portfolio a:hover .field-flex-wrap {
  background-color: rgba(24, 39, 46, 0.7);
}
.view-portfolio a:hover .description {
  opacity: 1;
}

.view-tags .views-row {
  display: inline;
}
.view-tags .views-field {
  display: inline;
}

