.slick--skin--default {
  background-image: url(../images/devices/cs-imac-frame.png);
  background-repeat: no-repeat;
  background-position: center top;
  height: 611px;
  width: 846px;
  margin: 30px auto;
  padding: 27px 82px 197px 83px;
  overflow: hidden;
}
.slick--skin--default .slide__caption {
  position: relative;
  padding: 5px 0;
  opacity: .8;
  top: -50px;
  text-align: center;
  margin: 0 auto;
  width: 40%;
  background: #fff;
}
@media (max-width: 992px) {
  .slick--skin--default {
    background: none transparent;
    padding: 0;
    width: 100%;
    height: auto;
    margin: 15px auto 5px auto;
  }
  .slick--skin--default img {
    width: 100%;
    height: auto;
  }
}
.slick--skin--default .lazy {
  height: 385px !important;
}

.slick__arrow button {
  outline: 0;
}

.slick-arrow {
  padding: 0;
  background: transparent;
  border: 0;
  color: #414a51;
  top: 38%;
  position: absolute;
  width: 110px;
  height: 36px;
  fill: currentColor;
  font-size: 20px;
  font-weight: 100;
  overflow: hidden;
  text-align: center;
}
.slick-arrow.slick-prev {
  left: 0;
}
.slick-arrow.slick-prev:before {
  font-family: 'Glyphicons Halflings', sans-serif;
  content: "\e079";
  margin-right: 5px;
  top: 3px;
  position: relative;
}
.slick-arrow.slick-next {
  right: 0;
}
.slick-arrow.slick-next:after {
  font-family: 'Glyphicons Halflings', sans-serif;
  content: "\e080";
  margin-left: 5px;
  top: 3px;
  position: relative;
}
@media (max-width: 992px) {
  .slick-arrow {
    top: 31%;
    background: #ddd;
    opacity: .8;
  }
}

